import format from "date-fns/format";
import getDay from "date-fns/getDay";
import parse from "date-fns/parse";
import startOfWeek from "date-fns/startOfWeek";
import React, { useState } from "react";
import { Calendar, dateFnsLocalizer } from "react-big-calendar";
import "react-big-calendar/lib/css/react-big-calendar.css";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import "./App.css";
import validator from 'validator';

const locales = {
    "en-US": require("date-fns/locale/en-US"),
};
const localizer = dateFnsLocalizer({
    format,
    parse,
    startOfWeek,
    getDay,
    locales,
});

const events = [
    // {
    //     title: "Big Meeting",
    //     allDay: true,
    //     start: new Date(2021, 6, 0),
    //     end: new Date(2021, 6, 0),
    // },
    // {
    //     title: "Vacation",
    //     start: new Date(2021, 6, 7),
    //     end: new Date(2021, 6, 10),
    // },
    // {
    //     title: "Conference",
    //     start: new Date(2021, 6, 20),
    //     end: new Date(2021, 6, 23),
    // },
];

function App() {
    const [newEvent, setNewEvent] = useState({ title: "", start: "", end: "" });
    const [allEvents, setAllEvents] = useState(events);
    const [validate,setValidate] = useState('none')

    function handleAddEvent() {
        let dateValidator = validator.isAfter(newEvent.end.toString(),newEvent.start.toString());
        if (dateValidator) {
          setAllEvents([...allEvents, newEvent]);
          setValidate('none')
        }
        else{
          setValidate('block')
        }
        
    }

    return (
        <div className="App">
            <h1>Booking</h1>
            {/* <h2>Add New Event</h2> */}
       
            <div className="container">
                
                <div>
                  <h4>Name</h4>
                <input type="text" className="Name" placeholder="Add Name"  value={newEvent.title} onChange={(e) => setNewEvent({ ...newEvent, title: e.target.value })} />
                </div>
                <div>
                  <h4>Check-in</h4>
                <DatePicker placeholderText="check-in Date" style={{ marginRight: "10px" }} selected={newEvent.start} onChange={(start) => setNewEvent({ ...newEvent, start })} />
                </div>
                <div>
                  <h4>Check-out</h4>
                  <DatePicker placeholderText="check-out Date" selected={newEvent.end} onChange={(end) => setNewEvent({ ...newEvent, end })} />
                </div>
            </div>
                <button stlye={{ marginTop: "10px" }} onClick={handleAddEvent}>
                    Book Now
                </button>
                <h2 style={{display: `${validate}`,color:'red'}}>Enter a valid date</h2>
            <Calendar localizer={localizer} events={allEvents} startAccessor="start" endAccessor="end" style={{ height: 500, margin: "50px" }} />
        </div>
    );
}

export default App;
